import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CommonPaymentService } from '../serivces/common-payment.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'lib-payment-complete',
  templateUrl: './payment-complete.component.html',
  styleUrls: ['./payment-complete.component.scss']
})
export class PaymentCompleteComponent implements OnInit {

  @Input() valid:boolean=false;
  @Input() cardDetails:any;
  @Input() completePageDetails:any;
  pending: boolean=true;
  userResponse: any;
  paymentCharges: any;
  cardImg: string='';
  paymentFailed: boolean = false;
  loading:boolean=false;
  completePaymentData:any;
  items:any;
  balance:number=0;
  tip:number=0;
  uuid:any;
  amount:number=0;
  paymentType:string = '';

  @Output() close:EventEmitter<boolean> = new EventEmitter<boolean>();
  cardCharges:any;
  cardCharge: number=0;


  constructor(private commonPaymentService:CommonPaymentService,private activatedRoute: ActivatedRoute) { 
    this.activatedRoute.queryParams.subscribe(params => {
      this.cardCharge = parseFloat(params['cardCharges']); 
  });
  }

  ngOnInit(): void {
   this.cardCharges = this.completePageDetails.cardCharges[this.commonPaymentService.cardCharges].charge
   this.loading = false;
   console.log('completePaymentData',this.valid,this.completePageDetails);
   
    if((this.completePageDetails.paymentDetails?.cardlat4 || this.cardDetails) && this.valid){
      this.pending=false;
    }else{
      this.pending=false;
    }
  }

}
