import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CommonPaymentService {

  cardCharges: any=[];
  apiUrl:any;
  completePageData:any;
  private userUrl = new BehaviorSubject(null);
  userUrl$ = this.userUrl.asObservable();

  private paymentDetails = new BehaviorSubject(null);
  paymentDetails$ = this.paymentDetails.asObservable();

  constructor(private http:HttpClient) { }

  getCountryName() {
    return this.http.get(`https://restcountries.com/v3.1/all`)
  }

  setUserResponse(data: any,key:any){
    if(data.cardCharges){
      this.cardCharges = data.cardCharges.findIndex((item:any) => item.paymentIntegration === (data.isStripeEnabled?"Stripe":"Basys"))
    }
  }

  setPaymentDetails(data:any){
      this.completePageData = data;
      this.paymentDetails.next(data);
  }

  getApiUrl(url:any){
    console.log('apiUrl',url);
      this.userUrl.next(url);
      this.apiUrl = url;
  }

  getStorecard(invoiceAddressId: string) {
    var userdata = this.http.get(this.apiUrl.apiUrl + `invoiceaddresses/${invoiceAddressId}/storedcards`, { headers: { "token": this.apiUrl.token, "portal": this.apiUrl.portal } });
    return userdata

  }
}
